import React, { useState, useEffect} from 'react';
import Header from '../../modules/header.js';
import Footer from '../../modules/footer.js';
import axios from "axios";
import {useNavigate} from 'react-router-dom';
import LinearProgress from '@mui/material/LinearProgress';
import Box from '@mui/material/Box';
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";

export default function NewMedicalRecord() {
    const [hospital, setHospital] = useState("");
    const [doctor, setDoctor] = useState("");
    const [dateOfVisit, setDateOfVisit] = useState(new Date());
    const [date, setDate] = useState("");
    const [historyOfPresentIllness, setHistoryOfPresentIllness] = useState("");
    const [pastMedicalHistory, setPastMedicalHistory] = useState("");
    const [medications, setMedications] = useState("");
    const [allergies, setAllergies] = useState("");
    const [physicalExamination, setPhysicalExamination] = useState("");
    const [laboratoryResults, setLaboratoryResults] = useState("");
    const [radiologicalFindings, setRadiologicalFindings] = useState("");
    const [diagnosis, setDiagnosis] = useState("");
    const [treatment, setTreatment] = useState("");
    const [medicationPrescribed, setMedicationPrescribed] = useState("");
    const [followUp, setFollowUp] = useState("");
    const [additionalComments, setAdditionalComments] = useState("");
    const [name, setName] = useState("")
    const [isLoading, setIsLoading] = useState(false);
    const navigate = useNavigate();
    const [doctorDid, setDoctorDid] = useState("");
    const [patientDid, setPatientDid] = useState("");

    

    useEffect(() => {
        if(new URL(document.location.toString()).searchParams.get("patient"))
            setName(new URL(document.location.toString()).searchParams.get("patient"));
        
        setDoctorDid(localStorage.getItem("did"));
    }, []);

    useEffect(() => {
        const temp = dateOfVisit.toString().split(" ");
        let month = temp[1];
        if(month === "Jan") month = "1";
        else if(month === "Feb") month = "2";
        else if(month === "Mar") month = "3";
        else if(month === "Apr") month = "4";
        else if(month === "May") month = "5";
        else if(month === "Jun") month = "6";
        else if(month === "Jul") month = "7";
        else if(month === "Aug") month = "8";
        else if(month === "Sep") month = "9";
        else if(month === "Oct") month = "10";
        else if(month === "Nov") month = "11";
        else if(month === "Dec") month = "12";
        let _date = temp[3]+"/"+month+"/"+temp[2];
        setDate(_date);
        _date = "";
    }, [dateOfVisit]);

    const handleClick = () => {
        const recordData = {
            hospital: hospital,
            doctor: doctor,
            dateOfVisit: date,
            historyOfPresentIllness: historyOfPresentIllness,
            pastMedicalHistory: pastMedicalHistory,
            medications: medications,
            allergies: allergies,
            physicalExamination: physicalExamination,
            laboratoryResults: laboratoryResults,
            radiologicalFindings: radiologicalFindings,
            diagnosis: diagnosis,
            treatment: treatment,
            medicationPrescribed: medicationPrescribed,
            followUp: followUp,
            additionalComments: additionalComments,
        }
        setIsLoading(true);
        console.log(recordData)

        // jwt did       ,   
        //      //    

        //  jwt -> local.
        //  jwt -> qr .

        const serverIP = process.env.REACT_APP_SERVER_IP_ADDRESS;

        console.log("DID ::: ", doctorDid, patientDid);

        axios.post(`http://${serverIP}:5001/user/new-record`, {recordData, doctorDid, patientDid})
            .then(res => {
                isLoading(false);
                console.log("vcJwt: ", res.data.updatedVcJwt)
                localStorage.setItem("jwt", res.data.updatedVcJwt);
                navigate(`/patient-medical-records?patient=${name}`);
            })
    }

    return (
        <div className='root'>
            <Header />
            {
                isLoading ?
                <div className='column-center auth-loading'>
                    <p>  </p>
                    <Box sx={{ width: '60%' }}> 
                        <LinearProgress />
                    </Box>
                </div>
                :
                <div className='body column-center'>
                <p style={{ fontSize: '30px' }}> </p>
                <div className='input-container'>
                    <p className='input-title' 
                        style={{ marginBottom: '3vh' }}> </p>
    
                    {/*  */}
                    <div className='input-field row'>
                        <div className='desc-container-1'>
                            <p className='desc'></p>
                        </div>
                        <DatePicker 
                            className='date-picker'
                            dateFormat="yyyy/MM/dd"
                            selected={dateOfVisit} 
                            onChange={(date) => 
                                setDateOfVisit(date)}
                            />
                    </div>

                    {/*  */}
                    <div className='input-field row'>
                        <div className='desc-container-1'>
                            <p className='desc'></p>
                        </div>
                        <input 
                            className='input-1' 
                            value={name} 
                            onChange={(e) => 
                                setName(e.target.value)} 
                            />
                    </div>
    
                    {/*   */}
                    <div className='input-field row'>
                        <div className='desc-container-1'>
                            <p className='desc'> </p>
                        </div>
                        <input 
                            className='input-1' 
                            value={hospital} 
                                onChange={(e) => 
                                    setHospital(e.target.value)} 
                            />
                    </div>
    
                    {/*   */}
                    <div className='input-field row'>
                        <div className='desc-container-1'>
                            <p className='desc'> </p>
                        </div>
                        <input 
                            className='input-1' 
                            value={doctor} 
                            onChange={(e) => 
                                setDoctor(e.target.value)} 
                            />
                    </div>
    
                    {/*   */}
                    <div className='input-field row'>
                        <div className='desc-container-1'>
                            <p className='desc'> </p>
                        </div>
                        <input 
                            className='input-1' 
                            value={historyOfPresentIllness} 
                            onChange={(e) => 
                                setHistoryOfPresentIllness(e.target.value)} 
                            />
                    </div>
    
                    {/*   */}
                    <div className='input-field row'>
                        <div className='desc-container-1'>
                            <p className='desc'> </p>
                        </div>
                        <input 
                            className='input-1' 
                            value={pastMedicalHistory} 
                            onChange={(e) => 
                                setPastMedicalHistory(e.target.value)} 
                            />
                    </div>
    
                    {/*    */}
                    <div className='input-field row'>
                        <div className='desc-container-1'>
                            <p className='desc'>  </p>
                        </div>
                        <input 
                            className='input-1' 
                            value={medications} 
                            onChange={(e) => 
                                setMedications(e.target.value)} 
                            />
                    </div>
    
                    {/*   */}
                    <div className='input-field row'>
                        <div className='desc-container-1'>
                            <p className='desc'> </p>
                        </div>
                        <input 
                            className='input-1' 
                            value={allergies} 
                            onChange={(e) => 
                                setAllergies(e.target.value)} 
                            />
                    </div>
    
                    {/*   */}
                    <div className='input-field row'>
                        <div className='desc-container-1'>
                            <p className='desc'> </p>
                        </div>
                        <input className='input-1' 
                            value={physicalExamination} 
                            onChange={(e) => 
                                setPhysicalExamination(e.target.value)} 
                            />
                    </div>
    
                    {/*   */}
                    <div className='input-field row'>
                        <div className='desc-container-1'>
                            <p className='desc'> </p>
                        </div>
                        <input 
                            className='input-1' 
                            value={laboratoryResults} 
                            onChange={(e) => 
                                setLaboratoryResults(e.target.value)} 
                            />
                    </div>
    
                    {/*    */}
                    <div className='input-field row'>
                        <div className='desc-container-1'>
                            <p className='desc'>  </p>
                        </div>
                        <input 
                            className='input-1' 
                            value={radiologicalFindings} 
                            onChange={(e) => 
                                setRadiologicalFindings(e.target.value)} 
                            />
                    </div>
    
                    {/*  */}
                    <div className='input-field row'>
                        <div className='desc-container-1'>
                            <p className='desc'></p>
                        </div>
                        <input 
                            className='input-1' 
                            value={diagnosis} 
                            onChange={(e) => 
                                setDiagnosis(e.target.value)} 
                            />
                    </div>
    
                    {/*  */}
                    <div className='input-field row'>
                        <div className='desc-container-1'>
                            <p className='desc'></p>
                        </div>
                        <input 
                            className='input-1' 
                            value={treatment} 
                            onChange={(e) => 
                                setTreatment(e.target.value)} 
                            />
                    </div>
    
                    {/*   */}
                    <div className='input-field row'>
                        <div className='desc-container-1'>
                            <p className='desc'> </p>
                        </div>
                        <input 
                            className='input-1' 
                            value={medicationPrescribed} 
                            onChange={(e) => 
                                setMedicationPrescribed(e.target.value)} 
                            />
                    </div>
    
                    {/*   */}
                    <div className='input-field row'>
                        <div className='desc-container-1'>
                            <p className='desc'> </p>
                        </div>
                        <input 
                            className='input-1' 
                            value={followUp} 
                            onChange={(e) => 
                                setFollowUp(e.target.value)} 
                            />
                    </div>
    
                    {/*   */}
                    <div className='input-field row'>
                        <div className='desc-container-2'>
                            <p className='desc'> </p>
                        </div>
                        <textarea 
                            className='input-2' 
                            value={additionalComments} 
                            onChange={(e) => 
                                setAdditionalComments(e.target.value)} 
                            />
                    </div>

                    <div className='row-center'>
                        <button className='submit-button' 
                                onClick={handleClick}>
                            
                        </button>
                    </div>
                </div>
            </div>
            }
            <Footer />
        </div>
    );
}    
                    
