import React, { useState, useEffect} from 'react';
import axios from "axios"
import Header from '../../modules/header.js';
import Footer from '../../modules/footer.js';
import './doctor-auth.css';

export default function DoctorAuth() {
    // isDoctor true, Doctor      - axios
    // doctorList  
    //   axios   
    // Doctor  
    // jwt         //      //   
    const [waitingList, setWaitingList] = useState([]);
    const serverIP = process.env.REACT_APP_SERVER_IP_ADDRESS;


    useEffect(() => {
        async function fetchData() {
            try {
                const response = await axios.get(`http://${serverIP}:5001/user/get-doctor-waiting-list`);
                setWaitingList(response.data);
                console.log(waitingList)
            } catch (error) {
                console.log(error);
            }
        }
        fetchData();
    }, []);

    return(
        <div className='root'>
            <Header />
                <div className='column-center auth-box'>
                <p style={{fontSize:'30px'}}> </p>
                    <div className='records-box'>
                        <div className='records-index'>
                            <p className='records-index-date'></p>
                            <p className='records-index-hpt'></p>
                            {/* <p className='records-index-notes'> </p> */}
                        </div>
                        { waitingList.map((item, index) => {
                            return (
                                <div className={`records-list pointer`} 
                                    key={index} >
                                    <div className='records-list-date'>
                                        <p>{item.name}</p>
                                    </div>
                                    <div className='records-list-hpt'>
                                        <p>{item.birthday}</p>
                                    </div>
                                    <input 
                                        type="checkbox"
                                        className='auth-checkbox' 
                                        onClick={() => {
                                            console.log(item)   
                                            axios.post(`http://${serverIP}:5001/doctor/new-doctor`, item)
                                            .then(res => {
                                                console.log(res.data) //   jwt 
                                            }).catch(console.log)
                                        }}/>
                                </div>
                            )
                        }) }

                    </div>
                </div>
            <Footer />
        </div>
    )
}
