import React, { useState, useEffect} from 'react';
import { useNavigate } from 'react-router-dom';

export default function Header() {
    const navigate = useNavigate();
    const [menu, setMenu] = useState([]);
    const isDoctor = sessionStorage.getItem("isDoctor");

    useEffect(() => {
        if(isDoctor)
            setMenu([
                {title:'QR ', route:'qr-code-scan'}, 
                {title:' ', route:'patient-list'}, 
                {title:' ', route:'doctor-auth'}
            ]);
        else    
            setMenu([
                {title:'QR', route:'qr-code'},
                {title:' ', route:'medical-records'}
            ]);
        
    }, [])

    
    const login = sessionStorage.getItem('login');
    const name = sessionStorage.getItem("name");

    return (
        <div className='header'>
                <p className='header-title pointer' onClick={()=>{navigate('/')}}>DMRS</p>
                <div className='header-menu'>
                    { menu.map((pages) => {
                        return (
                            <div className='header-menu-buttons pointer row-center'>
                                <p className='header-menu-font' 
                                    onClick={() => {
                                        if(login)
                                            navigate(`/${pages.route}`);
                                        else 
                                            navigate("/login");
                                    }} >{pages.title}</p>
                            </div>
                        );
                    }) }
                </div>
                <div className='header-user-info pointer row-center'>
                    { login ? 
                        <p onClick={(() => {sessionStorage.clear(); navigate('/');})}>{name}</p> 
                        : 
                        <p onClick={() => {
                            navigate('/login');
                        }}></p> }
                </div>
            </div>
    )
}
